// 把属性解构出来，便于 delete
const cloneData = (data: Record<string, any>) => {
  let newData = {
    ...data,
  };

  if (data.list) {
    newData = {
      ...data,
      list: {
        ...data.list,
        symbolStyle: {
          ...data.list.symbolStyle,
        },
      },
    };
  }

  return newData;
};

export default cloneData;

